            <img src="../../assets/image/entrust.png" alt="" style="width: 0.44rem;vertical-align: middle"> -->
          </div>
        </div>
      </div>
      <div class="zhanwei"></div>
      <div class="middle">
        <div class="left-right-box">
          <div class="middle-lef-box">
            <!--第2行 +-价格-->
            <div class="iptBox">
              <div style="flex: 1;" class="rideoBox">
                <van-radio-group v-model="is_status" direction="horizontal">
                <van-radio name="1">
                  <!-- 限价 -->
                  {{$t('tradeVue.xianjia')}}
                  </van-radio>
                <van-radio name="2">
                  <!-- 市价 -->
                  {{$t('tradeVue.shijia')}}
                  </van-radio>
              </van-radio-group>
              </div>
            </div>
            <div class="row-c select-num">
              <div class="" style="text-align: left;text-indent: 0.2rem;height: 0.6rem;line-height: 0.6rem;background: rgba(18,23,43,1);border-radius: 0.1rem;color: rgba(255,255,255,0.5)">
                {{isAmount}}
              </div>
            </div>
            <div class="row-d">
              <input v-if="this.$store.state.language=='en'" type="number" v-model="iptText" @input="changeInput" placeholder="amount" style="height: 0.6rem;line-height: 0.3rem;text-indent: 0.2rem;color: rgba(255, 255, 255, 0.5);width: 100%;border:none;font-size: 0.28rem;background-color: #12172B;">
              <input v-else type="number" v-model="iptText" @input="changeInput" placeholder="数量" style="height: 0.6rem;line-height: 0.3rem;text-indent: 0.2rem;color: rgba(255, 255, 255, 0.5);width: 100%;border:none;font-size: 0.28rem;background-color: #12172B;">
            </div>
            <div class="row-d">
              <input v-if="this.$store.state.language=='en'" type="password" v-model="paypass" @input="changeInput" placeholder="enter transaction password" style="height: 0.6rem;line-height: 0.3rem;text-indent: 0.2rem;color: rgba(255, 255, 255, 0.5);width: 100%;border:none;font-size: 0.28rem;background-color: #12172B;">
              <input v-else type="password" v-model="paypass" @input="changeInput" placeholder="请输入交易密码" style="height: 0.6rem;line-height: 0.3rem;text-indent: 0.2rem;color: rgba(255, 255, 255, 0.5);width: 100%;border:none;font-size: 0.28rem;background-color: #12172B;">
            </div>
            <!--第5行  总额-->
            <div class="total-num">
              <!-- 总额 -->
              {{$t('common.total')}}
               [ {{totalAmount}} ]
            </div>
            <!--            第六行  可用-->
            <div class="row-e">
              <span>
                <!-- 可用 -->
                {{$t('tradeVue.keyong')}}
                </span>
