            </div>
            <!--            第七行  可用-->
            <div class="row-f" v-if="u_id ==''">
              登录
            </div>
            <div class="row-f" @click="purchase()" v-if="u_id !='' && isBusiness == 0">
              <!-- 买入 -->
              {{$t('common.buy_in')}}
            </div>
            <div class="row-f activeBut" @click="bibibuyFun()" v-if="u_id !='' && isBusiness == 1 ">
              {{$t('common.buy_out')}}
            </div>
            <!--            第八行  买盘 卖盘 标志-->
