              <span></span>
              <!-- 买盘 -->
              {{$t('tradeVue.buy_pan')}}
              <span style="margin-left: 0.33rem;background:linear-gradient(0deg,rgba(250,76,69,1),rgba(222,61,102,1));"></span>
              <!-- 卖盘 -->
              {{$t('tradeVue.sell_pan')}}
