            <div class="middle-right-num-list">
              <div v-show="index < 6" class="list-item" v-for="(item, index) in depthList.bids" :key="index">
                <span class="red-font">{{FunText(item[0])}}</span>
                <span style="color: rgba(255,255,255,0.7);">{{FunText(item[1])}}</span>
                <div class="mask1"></div>
              </div>
