              {{current_price_usd}}U ≈ ￥{{(current_price_usd*7).toFixed(2)}}
            </div>
            <!--         中间右边 第二部分数字展示  绿色 -->
            <div class="middle-right-num-list">
              <div class="list-item"  v-show="index < 6"  v-for="(item, index) in depthList.asks" :key="index">
                <span class="green-font">{{FunText(item[0])}}</span>
                <span style="color: rgba(255,255,255,0.7);">{{FunText(item[1])}}</span>
