          <div class="right-text-icon">
<!--            <span style="">全部</span>-->
            <!-- <van-icon name="arrow" style="vertical-align: middle" /> -->
          </div>
        </div>
        <!--        thead 区域  时间 价格 数量-->
        <div class="thead">
          <span>
            <!-- 时间 -->
            {{$t('common.time')}}
            </span>
