            <!-- 价格 -->
            {{$t('common.price')}}
            </span>
          <span>
            <!-- 数量 -->
            {{$t('common.number')}}
            </span>
        </div>
        <!--        底部数据展示区域-->
        <div class="num-item" v-for="(item,index) in buslogList" :key="index">
          <span>{{item.T}}</span>
          <span :class="index%2==0?'green-font':'red-font'">{{item.p}}</span>
          <span>{{item.q}}</span>
        </div>
