</template>

<script>
import Vue from 'vue'
import { coin, depth, buslog, bibisell, bibibuy } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import { DropdownMenu, DropdownItem, Icon, RadioGroup, Radio, Toast } from 'vant'
Vue.use(DropdownMenu).use(DropdownItem).use(Icon).use(RadioGroup).use(Radio).use(Toast)
export default {
  components: {},
  data () {
    return {
      iptText: '',
      is_status: '1',
      isSelect: 0,
      isBusiness: 0,
      value1: 0,
      value2: 'a',
      userCurrency: '',
      option1: [],
      option: [],
      paypass: '',
      parameter: {
        market: 'coin',
        page: 1,
        showpage: 10
      },
      depthList: [],
      purchaseData: {
        market: 'onlycoin',
        symbol: 'btc'
      },
      depthData: {
        market: 'depth',
        symbol: 'btc'
      }, // 盘口对象
      buslogData: {
        market: 'log',
        symbol: 'btc'
      }, // 成交量交易量对象
      buslogList: [],
      // 买入
      bibisellData: {
        u_id: '10', // 用户ID
        symbol: 'btc', // 币种
        price: '', // 消耗数量
        num: 1, // 消耗数量
        paypass: '12345678' // 交易密码
      },
      // 卖出
      bibibuyData: {
        u_id: '10',
        symbol: 'btc', // 币种
        price: '', // 获得数量
        num: 1, // 消耗数量
        paypass: '12345678' // 交易密码
      },
      list: [],
      u_id: '',
      coinTypeList: [],
      totalAmount: '0.0000', // 总额度
      isScale: '', // 选中的幅度
      isAmount: '',
      usdt_available_balance: '', // 可用余额
      current_price_usd: '' //
    }
  },
  created () {
    // sessionStorage.setItem('usdt_available_balance', res.data.usdt_available_balance)
    this.u_id = sessionStorage.getItem('u_id')
    this.bibisellData.u_id = sessionStorage.getItem('u_id')
    this.bibibuyData.u_id = sessionStorage.getItem('u_id')
    // 可用余额 usdt
    this.usdt_available_balance = sessionStorage.getItem('usdt_available_balance')
    var data = objKeySort(this.parameter)
    var token = sgin(data)
    this.parameter.token = token
    this.marketList(this.parameter) // 下拉币种
    this.depthFun() // 盘口
    this.buslogFun() // 交易量
    // this.FunText('200.000000') //
    // this.coinType()
  },
  methods: {
    FunText (type) {
      var text = Number(type)
      var conTxt = text.toFixed(4)
      // console.log(conTxt, '212111')
      return conTxt
    },
    // 初始化币种市场
    marketList (parameter) {
      var that = this
      coin(parameter).then(res => {
        console.log('res市场', res)
        that.list = res.data
        for (const [i, v] of res.data.entries()) {
          var arr = {
            text: '',
            value: 0,
            current_price_usd: '',
            scale: ''
          }
          if (v.name === 'USDT/USDT') {
            console.log('等于了')
          } else {
            arr.text = v.name
            arr.value = i
            arr.current_price_usd = v.current_price_usd
            arr.scale = v.scale
            that.option.push(arr)
          }
          that.userCurrency = that.option[0].text
          that.isScale = that.option[0].scale
          that.isAmount = that.option[0].current_price_usd
          that.current_price_usd = that.option[0].current_price_usd
        }
      }).catch((err) => console.log(err))
    },
    // 交易量
    buslogFun () {
      var that = this
      var data = objKeySort(this.buslogData)
      var token = sgin(data)
      this.buslogData.token = token
      buslog(that.buslogData).then(res => {
        if (res.code === 0) {
          console.log('交易量', res)
          that.buslogList = res.data
          delete that.buslogData.token
        }
      }).catch((err) => {
        console.log(err)
        delete that.buslogData.token
      })
    },
    // 盘口列表
    depthFun () {
      var that = this
      var data = objKeySort(this.depthData)
      var token = sgin(data)
      this.depthData.token = token
      depth(this.depthData).then(res => {
        if (res.code === 0) {
          this.depthList = res.data
          // console.log('盘口列表1', res.data.bids)
          delete that.depthData.token
        }
      }).catch((err) => {
        console.log(err)
        delete that.depthData.token
      })
    },
    // 计算总额度
    changeInput () {
      var that = this
      this.$nextTick(() => {
        console.log(that.iptText, that.isAmount)
        this.totalAmount = (Number(that.isAmount) * Number(that.iptText)).toFixed(4)
      })
    },
    // 点击买入
    purchase () {
      var that = this
      that.bibisellData.price = that.totalAmount
      that.bibisellData.num = that.iptText
      that.bibisellData.paypass = that.paypass
      var data = objKeySort(this.bibisellData)
      var token = sgin(data)
      this.bibisellData.token = token
      bibisell(that.bibisellData).then(res => {
        if (res.code === 0) {
          // this.depthList = res.data
          that.$toast({
            message: '买入成功',
            duration: 1500
          })
          that.bibisellData.price = ''
          that.bibisellData.num = ''
          that.bibisellData.paypass = ''

          that.bibisellData.price = that.totalAmount
          that.usdt_available_balance -= that.totalAmount
          delete that.bibisellData.token
        }
      }).catch((err) => {
        that.$toast({
          message: err,
          duration: 1500
        })
        // that.bibisellData.symbol = ''
        that.bibisellData.price = ''
        that.bibisellData.num = ''
        that.bibisellData.paypass = ''
        delete that.bibisellData.token
      })
    },
    // 卖出
    bibibuyFun () {
      var that = this
      console.log(that.totalAmount, 'that.totalAmount')
      that.bibibuyData.price = that.totalAmount
      that.bibibuyData.paypass = that.paypass
      console.log(that.bibibuyData.price, 'that.bibibuyData.price', that.bibibuyData.paypass, 'paypass')
      that.bibibuyData.num = that.iptText
      var data = objKeySort(this.bibibuyData)
      var token = sgin(data)
      that.bibibuyData.token = token
      console.log(that.bibibuyData, 'bibibuyData')
      bibibuy(that.bibibuyData).then(res => {
        if (res.code === 0) {
          // this.depthList = res.data
          that.$toast({ message: '卖出成功', duration: 1500 })
          that.bibibuyData.price = ''
          that.bibibuyData.num = ''
          that.bibibuyData.paypass = ''
          that.usdt_available_balance = Number(that.usdt_available_balance) + Number(that.totalAmount)
          // console.log('盘口列表1', res.data.data)
          delete that.bibibuyData.token
        }
      }).catch((err) => {
        console.log(err, '11')
        that.$toast({ message: err, duration: 1500 })
        // that.bibisellData.symbol = ''
        that.bibibuyData.price = ''
        that.bibibuyData.num = ''
        that.bibibuyData.paypass = ''
        delete that.bibibuyData.token
      })
    },
    ccc (value) {
      console.log('type', value)
      var that = this
      for (const [i, v] of that.option.entries()) {
        if (v.value === value) {
          console.log('等于了', i, v, v)
          that.userCurrency = v.text
          that.isAmount = v.current_price_usd
          that.isScale = v.scale
          that.current_price_usd = v.current_price_usd
        }
      }
    },
    userSelect (type) {
      this.isSelect = type
      if (type === 2) {
        this.$router.push({
          path: '/contract1',
          query: {
            bibi: 'heyue'
          }
        })
      }
    },
    butBusiness (type) {
      this.isBusiness = type
    },
    toOrder () {
      this.$router.push({
        path: '/personal/order',
        query: {
          types: '币币'
        }
      })
    }
  }
}
</script>
